import globalAxios, { AxiosPromise, AxiosInstance } from 'axios'
import { createRequestFunction } from 'lightcone_api/api/common'
import {
  BASE_PATH,
  COLLECTION_FORMATS,
  RequestArgs,
  BaseAPI,
  RequiredError,
} from 'lightcone_api/api/base'

import { UpdateAccountRequestV3 } from 'lightcone_api/api'

import * as api from 'lightcone_api/api'

import { getEdDSASig, getEdDSASigWithPoseidon, getEcDSASig } from 'lightcone_api/api_wrapper/sign_tools'

class AuthHasEcdsaSigApi {

    localVarAxiosParamCreator: any
    conf: any

    constructor(conf: any) {
        this.conf = conf
        this.localVarAxiosParamCreator = api.AuthHasEcdsaSigApiAxiosParamCreator(conf)
    }

    async submitUpdateAccount(exAddr: string, owner: string, accountId: number, PublicKeyX: string, PublicKeyY: string, 
        tokenId: string, volume: string, validUntil: number, nonce: number, web3: any, hasDataStruct: boolean = true) {

        const reqData: UpdateAccountRequestV3 = {
          exchange: exAddr,
          owner: owner,
          accountId: accountId,
          publicKey: {
            x: PublicKeyX,
            y: PublicKeyY,
          },
          maxFee: {
            tokenId: tokenId,
            volume: volume,
          },
          validUntil: validUntil,
          nonce: nonce,
        }

        const localVarAxiosArgs = await this.localVarAxiosParamCreator.submitUpdateAccount(
          reqData
        )

        const ecdsaSig = await getEcDSASig(web3, reqData, owner, '', hasDataStruct)

        console.log('ecdsaSig:', ecdsaSig)

        localVarAxiosArgs.options.headers['X-API-SIG'] = ecdsaSig

        const request = createRequestFunction(
          localVarAxiosArgs,
          globalAxios,
          BASE_PATH,
          this.conf
        )

        return await request()
        
    }

}

export { AuthHasEcdsaSigApi }
