import { useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'

import { RootState } from 'stores'

import { authNoKeyApi } from 'lightcone_api/api_wrapper'

import { setMarkets, setExchangeInfo, } from 'stores/trading/reducer'

export function useGetTradingInfo() {

    const tradingInfo = useSelector((state: RootState) => state.trading)

    return tradingInfo

}

export function useUpdateExchangeMarkets() {

    console.log('app useExchangeMarkets')

    const dispatch = useDispatch()

    useEffect(() => {

        authNoKeyApi.getMarkets().then((response) => {
    
            var symbols: any[] = []
            
            response.data.markets.forEach((item: any, index: number, array: any)=>{
                symbols.push({'title': item.market, 'link': item.market})
            })
    
            dispatch(setMarkets(symbols))
    
        }, (reject) => {

            console.log(reject)
    
        })

    })

}

export function useUpdateExchangeInfo() {

    const dispatch = useDispatch()

    useEffect(() => {

        authNoKeyApi.getExchangeInfoWrapper().then((response) => {
            dispatch(setExchangeInfo(response))
        }, (reject) => {

            console.log(reject)
    
        })

    })

}

export function useUpdateTokenTicker(market: string) {

    console.log('app useTokenTicker')

    const dispatch = useDispatch()

    useEffect(() => {

        authNoKeyApi.getTicker(market).then((response) => {
            
            console.log(response.data)
    
        }, (reject) => {

            console.log(reject)
    
        })

    })

}
