import globalAxios, { AxiosPromise, AxiosInstance } from 'axios'
import { createRequestFunction } from 'lightcone_api/api/common'

import { GetApiKeyRequest, SubmitOrderRequestV3 } from 'lightcone_api/api'

import {
  BASE_PATH,
  COLLECTION_FORMATS,
  RequestArgs,
  BaseAPI,
  RequiredError,
} from 'lightcone_api/api/base'

import * as api from 'lightcone_api/api'

import { getEdDSASig, getEdDSASigWithPoseidon, getEcDSASig } from 'lightcone_api/api_wrapper/sign_tools'

class AuthHasEddsaSigApi {

  localVarAxiosParamCreator: any
  conf: any
  basePath: string

  constructor(conf: any) {
    this.conf = conf

    this.basePath = this.conf.basePath ? this.conf.basePath : BaseAPI
    this.localVarAxiosParamCreator = api.AuthHasEddsaSigApiAxiosParamCreator(conf)
  }

  async getApiKey(PrivateKey: string, accountId: number) {

    const localVarAxiosArgs = await this.localVarAxiosParamCreator.getApiKey(
      accountId
    )

    const requestMap: Map<string, any> = new Map()
    requestMap.set('accountId', accountId)

    const eddsaSig = getEdDSASig(localVarAxiosArgs.options.method, this.basePath, '/api/v3/apiKey', requestMap, PrivateKey)

    localVarAxiosArgs.options.headers['X-API-SIG'] = eddsaSig

    const request = createRequestFunction(
      localVarAxiosArgs,
      globalAxios,
      this.basePath,
      this.conf
    )

    return await request()

  }

  async applyApiKey(PrivateKey: string, accountId: number) {

    const reqData: GetApiKeyRequest = {
      accountId: accountId
    }

    const localVarAxiosArgs = await this.localVarAxiosParamCreator.applyApiKey(reqData)

    const requestMap: Map<string, any> = new Map()
    requestMap.set('accountId', accountId)

    const eddsaSig = getEdDSASig(localVarAxiosArgs.options.method, this.basePath, '/api/v3/apiKey', requestMap, PrivateKey)

    // localVarAxiosArgs.options.headers['X-API-KEY'] = 'r70Bbf5eUzvyicHUBLqBMvqGjdMkHwU3W7FEeQGXJ7zRh4ML31EXI9N7Ef94K7ya'
    localVarAxiosArgs.options.headers['X-API-SIG'] = eddsaSig

    const request = createRequestFunction(
      localVarAxiosArgs,
      globalAxios,
      this.basePath,
      this.conf
    )

    return await request()

  }

  async submitOrderV3(PrivateKey: string, apiKey: string, exchange: string, accountId: number, storageId: number, sell_tokenId: string, sell_volume: string,  buy_tokenId: string, buy_volume: string, 
    allOrNone: string, fillAmountBOrS: string, validUntil: number, maxFeeBips: number) {

    const inputs = {}

    const eddsaSig = getEdDSASigWithPoseidon(inputs, PrivateKey)

    const reqData: SubmitOrderRequestV3 = {
      exchange: exchange,
      accountId: accountId,
      storageId: storageId,
      sellToken: {
        tokenId: sell_tokenId,
        volume: sell_volume
      },
      buyToken:  {
        tokenId: buy_tokenId,
        volume: buy_volume
      },
      allOrNone: allOrNone,
      fillAmountBOrS: fillAmountBOrS,
      validUntil: validUntil,
      maxFeeBips: maxFeeBips,
      eddsaSignature: eddsaSig,
    }

    const localVarAxiosArgs = await this.localVarAxiosParamCreator.submitOrderV3(reqData)

    localVarAxiosArgs.options.headers['X-API-SIG'] = eddsaSig

    const request = createRequestFunction(
      localVarAxiosArgs,
      globalAxios,
      this.basePath,
      this.conf
    )

    return await request()

  }

  async cancelOrder(PrivateKey: string, apiKey: string, accountId: number, orderHash?: string, clientOrderId?: string) {

    const localVarAxiosArgs = await this.localVarAxiosParamCreator.cancelOrder(accountId, orderHash, clientOrderId)

    console.log('localVarAxiosArgs:', localVarAxiosArgs)

    const requestMap: Map<string, any> = new Map()

    requestMap.set('accountId', accountId)
    if (orderHash)
      requestMap.set('orderHash', orderHash)
    if (clientOrderId)
      requestMap.set('clientOrderId', clientOrderId)

    const eddsaSig = getEdDSASig(localVarAxiosArgs.options.method, this.basePath, '/api/v3/order', requestMap, PrivateKey)

    console.log(localVarAxiosArgs)

    localVarAxiosArgs.options.headers['X-API-KEY'] = apiKey
    localVarAxiosArgs.options.headers['X-API-SIG'] = eddsaSig

    const request = createRequestFunction(
      localVarAxiosArgs,
      globalAxios,
      this.basePath,
      this.conf
    )

    return await request()

  }

}

export { AuthHasEddsaSigApi }
