import * as api from 'lightcone_api/api'
import { AccountInfo, } from 'defs/account_defs'
import { TickerData, DepthData, TradesData, ExchangeInfo, } from 'defs/exchange_defs'

import BigNumber from 'bignumber.js'

class AuthNoKeyApi extends api.AuthNoKeyApi {

    public async getAccountWrapper(owner: string, options?: any) {
        const raw_data = (await super.getAccount(owner, options)).data

        const info: AccountInfo = {
            accountId: raw_data.accountId,
            publicKey: raw_data.publicKey,
            owner: raw_data.owner,
            nonce: raw_data.nonce,
            fronzen: raw_data.fronzen == 'true'
        }

        return info
    }

    public async getExchangeInfoWrapper(options?: any) {
        const raw_data = (await super.getExchangeInfo(options)).data

        const info: ExchangeInfo = {
            ammExitFees: raw_data.ammExitFees,
            chainId: raw_data.chainId,
            depositAddress: raw_data.depositAddress,
            exchangeAddress: raw_data.exchangeAddress,
            fastWithdrawalFees: raw_data.fastWithdrawalFees,
            onchainFees: raw_data.onchainFees,
            openAccountFees: raw_data.openAccountFees,
            transferFees: raw_data.transferFees,
            updateFees: raw_data.updateFees,
            withdrawalFees: raw_data.withdrawalFees,
        }
        return info
    }

    public async getTickerWrapper(market: string[], options?: any) {
        const market_str = market.join(',')
        const raw_data = (await super.getTicker(market_str, options)).data['tickers']
        var tick_map: any = {}
        raw_data.forEach((item: any, ind: number, arr: any) => {
            const tick: TickerData = {
                date_time: new Date(parseInt(item[1])),
                base_token_amt: parseInt(item[2]),
                quote_token_amt: parseInt(item[3]),
                open: parseFloat(item[4]),
                high: parseFloat(item[5]),
                low: parseFloat(item[6]),
                close: parseFloat(item[7]),
                count: parseInt(item[8]),
                bid: parseFloat(item[9]),
                ask: parseFloat(item[10]),
            }
            tick_map[item[0]] = tick
        })

        return tick_map
    }

    private genAB(data: any[], isReverse: boolean = false) {

        var ab_arr: any[] = []
        var amtTotal = 0

        var ab_prices : any[] = []
        var ab_amtTotals :any [] = []
        
        data.forEach((item: any, ind: number, arr: any) => {
            const price = parseFloat(item[0])
            const amt = new BigNumber(item[1]).dividedBy(1e+18).toNumber()
            amtTotal += amt
            ab_arr.push({
                price: price,
                amt: amt,
                amtTotal: amtTotal,
            })
            ab_prices.push(price)
            ab_amtTotals.push(amtTotal)
        })

        if (isReverse) {
            ab_arr.reverse()
            ab_prices.reverse()
            ab_amtTotals.reverse()
        }

        return { ab_arr, ab_prices, ab_amtTotals }

    }

    public async getDepthWrapper(market: string, level: number = 2, limit: number = 50, options?: any) {
        const raw_data = (await super.getDepth(market, level, limit, options)).data
        const bids = this.genAB(raw_data['bids'])
        const asks = this.genAB(raw_data['asks'], true)

        const depth: DepthData = {
            version: parseInt(raw_data['version']),
            date_time: new Date(raw_data['timestamp']),
            bids: bids.ab_arr,
            bids_prices: bids.ab_prices,
            bids_amtTotals: bids.ab_amtTotals,
            asks: asks.ab_arr,
            asks_prices: asks.ab_prices,
            asks_amtTotals: asks.ab_amtTotals,
        }
        return depth
    }

    public async getMarketTradeWrapper(market: string, limit: number = 20, options?: any) {
        const raw_data = (await super.getMarketTrade(market, limit, options)).data
        const trades: TradesData = {
            totalNum: parseInt(raw_data['totalNum']),
            trades: raw_data['trades'],
        }
        return trades
    }

}

export { AuthNoKeyApi }
