import { useState, useEffect, useCallback } from 'react'
import { useDispatch, useSelector } from 'react-redux'

import { useWeb3React } from '@web3-react/core'
import { Web3Provider } from '@ethersproject/providers'

import { useGetTradingInfo } from 'stores/trading/hook'
import { useEagerConnect, useInactiveListener } from 'hooks/useWeb3'

import { authNoKeyApi } from 'lightcone_api/api_wrapper'

import { AccountStatus, POLLING_INTERVAL } from 'defs/account_defs'

import * as sign_tools from 'lightcone_api/api_wrapper/sign_tools'

import { ConnectorNames, connectorsByName } from 'defs/web3_defs'

import { RootState } from 'stores'
import { reset, setAccAddr, setAccountStatus, Lv2Account, setConnectName, setConnectNameTemp, setIsContractAddress, setAccountInfo, setEddsaKey, } from 'stores/account/reducer'

import Web3 from 'web3'

export function useWeb3Account() {
    const {
        connector,
        library,
        chainId,
        account,
        active,
        error,
        activate,
        deactivate,
    } = useWeb3React<Web3Provider>()

    const dispatch = useDispatch()

    const [activatingConnector, setActivatingConnector] = useState<any>()

    const showLv1 = active && account

    const connect = (item_name: ConnectorNames) => {
        const newConnector: any = connectorsByName[item_name]
        setActivatingConnector(newConnector)
        activate(newConnector)
        dispatch(setConnectNameTemp(item_name))
    }

    const disconnect = () => {
        deactivate()
        dispatch(reset(undefined))
    }

    return {
        chainId: chainId,
        connector: connector,
        provider: library,
        web3account: account,
        active: active,
        error: error,
        showLv1: showLv1,
        activatingConnector: activatingConnector,
        connect: connect,
        disconnect: disconnect,
    }
}

export function useCheckAccStatus() {

    const dispatch = useDispatch()

    const isExpired = false

    if (isExpired) {
        console.log('--> isExpired:')
    }

    const lv1Acc = useWeb3Account()

    const account: Lv2Account = useSelector((state: RootState) => state.account)

    useEffect(() => {

        if (!lv1Acc.web3account) {
            console.log('--> no web3account:', lv1Acc.web3account)
            return
        }

        console.log('--> got accAddr:', account.accAddr)
        console.log('--> got status:', account.status)
        console.log('--> got web3account:', lv1Acc.web3account)

        switch (account.status) {
            case AccountStatus.UNCONNNECTED:
                console.log('UNCONNNECTED web3account:', lv1Acc.web3account)
                dispatch(setAccAddr(lv1Acc.web3account))
                dispatch(setAccountStatus(AccountStatus.CONNECTED))
                break

            case AccountStatus.CONNECTED:
                //check session or local storage
                // dispatch(setAccAddr(lv1Acc.web3account))
                console.log('account.accAddr:', account.accAddr)
                console.log('lv1Acc.web3account:', lv1Acc.web3account)
                dispatch(setConnectName(account.connectNameTemp))
                const acc = account.accAddr

                if (acc && account.apikey) {
                    dispatch(setAccountStatus(AccountStatus.ACTIVATED))
                } else if (acc) {
                    // else try getAccount
                    authNoKeyApi.getAccountWrapper(acc).then((accInfo) => {
                        dispatch(setAccountInfo(accInfo))
                        // 1 has id and pubkey
                        if (accInfo.publicKey && accInfo.publicKey.X && accInfo.publicKey.Y) {
                            console.log('1 has id and pubkey currStatus:', account.status)
                            dispatch(setAccountStatus(AccountStatus.ACTIVATED))
                        }
                        // 2 has id but no pubkey
                        else {
                            console.log('has id but no pubkey currStatus:', account.status)
                            dispatch(setAccountStatus(AccountStatus.UNACTIVATED))
                        }
                    }, (reason) => {
                        // 3 has none of above
                        console.error(reason)
                        // check deposit tx (local and ethereum)
                        const hasTx = false
                        if (hasTx) {
                            console.log('hasTx currStatus:', account.status)
                            dispatch(setAccountStatus(AccountStatus.DEPOSITING))
                        } else {
                            console.log('!hasTx currStatus:', account.status)
                            dispatch(setAccountStatus(AccountStatus.NOACCOUNT))
                        }
                    })
                } else {
                    console.log('no acc')
                }
                break

            case AccountStatus.NOACCOUNT:
                break

            case AccountStatus.DEPOSITING:
                // TODO sub contract event, check deposit.
                const depositFinished = true // TODO
                if (depositFinished) {
                    console.log('depositFinished currStatus:', account.status)
                    dispatch(setAccountStatus(AccountStatus.DEPOSIT_TO_CONFIREM))
                }
                break

            case AccountStatus.DEPOSIT_TO_CONFIREM:
                // getAccount every 30s to check
                const deposit_check = setInterval(() => {

                    authNoKeyApi.getAccountWrapper(acc).then((accInfo) => {
                        console.log(accInfo)
                        // 1 has id and pubkey
                        if (accInfo.publicKey && accInfo.publicKey.X && accInfo.publicKey.Y) {
                            clearInterval(deposit_check)
                            console.log('1 has id and pubkey currStatus:', account.status)
                            dispatch(setAccountStatus(AccountStatus.UNACTIVATED))
                        }
                    }, (reason) => {
                        console.error(reason)
                    })

                }, POLLING_INTERVAL)
                break

            case AccountStatus.UNACTIVATED:
                const isSmartWallet = false // TODO
                if (isSmartWallet) {
                    // TODO approve hash,
                    console.log('approve hash, currStatus:', account.status)
                    dispatch(setAccountStatus(AccountStatus.ARPROVING))
                } else {
                    console.log('approve hash no smartwallet, account.status:', account.status)
                }
                break

            case AccountStatus.ARPROVING:
                // TODO sub contract event, check approving.
                const approved = false // TODO
                if (approved) {
                    console.log('sub contract event, check approving. approved! account.status:', account.status)
                    dispatch(setAccountStatus(AccountStatus.APPROV_TO_CONFIRM))
                }
                break

            case AccountStatus.APPROV_TO_CONFIRM:
                const approveConfirmed = false // TODO
                if (approveConfirmed) {
                    console.log('approveConfirmed account.status:', account.status)
                    dispatch(setAccountStatus(AccountStatus.ACTIVATED))
                }
                break

            case AccountStatus.LOCKED:
                break

            case AccountStatus.ACTIVATED:
                break

            default:
                break
        }

    }, [lv1Acc.web3account, account.status])

}

export function useGetLv2Account() {

    const account = useSelector((state: RootState) => state.account)

    console.log('useGetLv2Account account:', account)

    return {
        account,
    }

}

export function useConnectLv1Acc() {

    console.log('enter useConnectLv1Acc')

    const dispatch = useDispatch()

    const { connector, account, activate, error } = useWeb3React<Web3Provider>()

    // handle logic to recognize the connector currently being activated
    const [activatingConnector, setActivatingConnector] = useState<any>()
    useEffect(() => {
        if (activatingConnector && activatingConnector === connector) {
            setActivatingConnector(undefined)
        }
    }, [activatingConnector, connector])

    // handle logic to eagerly connect to the injected ethereum provider, if it exists and has granted access already
    const triedEager = useEagerConnect()

    // handle logic to connect in reaction to certain events on the injected ethereum provider, if it exists
    useInactiveListener(!triedEager || !!activatingConnector)

    const connect = (item_name: ConnectorNames) => {
        const newConnector: any = connectorsByName[item_name]
        setActivatingConnector(newConnector)
        activate(newConnector)
        dispatch(setConnectNameTemp(item_name))
    }

    return {
        connect,
    }

}

export function useUpdateLv2Account() {

    const lv1Account = useWeb3Account()

    const dispatch = useDispatch()

    const exchangeInfo = useGetTradingInfo().exchangeInfo

    useEffect(() => {
        if (lv1Account.active && lv1Account.web3account && lv1Account.provider !== undefined) {

            const web3account: string = lv1Account.web3account
            console.log('---web3account:', web3account)
            const exchangeAddress: string = exchangeInfo?.exchangeAddress
                ? exchangeInfo?.exchangeAddress
                : ''

            lv1Account.connector?.getProvider().then((provider) => {
                const web3 = new Web3(provider as any)

                if (exchangeAddress !== '' && web3account !== '') {

                    web3.eth.getCode(web3account).then((code) => {
                        if (code == '0x') {
                            dispatch(setIsContractAddress(false))
                        } else {
                            dispatch(setIsContractAddress(true))
                        }
                    })

                    authNoKeyApi.getAccountWrapper(web3account).then(
                        (data) => {
                            //get acccount from server succedded. gen eddsakey.
                            dispatch(setAccountInfo(data))
                            dispatch(setAccountStatus(AccountStatus.ACTIVATED))
                        },
                        (reject) => {
                            //get acccount from server failed.
                            console.log(reject)
                            dispatch(setAccountStatus(AccountStatus.UNACTIVATED))
                        }
                    )
                }

            })

        } else {
            console.log('back to UNCONNNECTED')
            dispatch(setAccountStatus(AccountStatus.UNCONNNECTED))
        }
    }, [
        lv1Account.active,
        lv1Account.web3account,
        exchangeInfo?.exchangeAddress,
    ])

}

export function useUnlockLv2Account() {

    const dispatch = useDispatch()

    const lv1acc = useWeb3Account()
    const lv2acc = useGetLv2Account()

    const exchangeInfo = useGetTradingInfo().exchangeInfo

    useEffect(() => {

        lv1acc.connector?.getProvider().then((provider) => {
            const web3 = new Web3(provider as any)

            sign_tools
                .generateKeyPair(
                    web3,
                    lv1acc.web3account as string,
                    exchangeInfo?.exchangeAddress as string,
                    lv2acc.account.accInfo.nonce,
                    lv2acc.account.connectName,
                )
                .then(
                    (eddsakey: any) => {
                        dispatch(setEddsaKey(eddsakey))
                        dispatch(setAccountStatus(AccountStatus.LOCKED))
                    },
                    (reject: any) => {
                        console.log(reject)
                    }
                ).catch((reason) => {
                    console.log(reason)
                })

        })
    }, [lv1acc.web3account, exchangeInfo?.exchangeAddress, lv2acc.account.accInfo.nonce])

}
