import { useState, useEffect } from 'react'
import { useTranslation } from 'react-i18next'

import { Tabs, Tab, Content } from 'components/tabs/HTabs'
import TradeRecordTable from 'components/tables/TradeRecordTable'

import { useGetTradingInfo } from 'stores/trading/hook'

import { authNoKeyApi } from 'lightcone_api/api_wrapper'
import { TradesData } from 'defs/exchange_defs'

export interface TradeRecordsPanelProps {
}

const TradeRecordsPanel = ({ }: TradeRecordsPanelProps) => {

    const { t } = useTranslation()

    const [active, setActive] = useState('0')

    const [trades, setTrades] = useState<TradesData>()

    const tradingInfo = useGetTradingInfo()

    const cols = [
        {
            Header: t('pages:trade_col_1') + '(' + tradingInfo.symbol_quote + ')',
            accessor: 'trade_col_1',
        },
        {
            Header: t('pages:trade_col_2'),
            accessor: 'trade_col_2',
        },
        {
            Header: t('pages:trade_col_3'),
            accessor: 'trade_col_3',
        },
    ]

    useEffect(() => {

        const id = setInterval(async () => {
            const trades_data = (await authNoKeyApi.getMarketTradeWrapper(tradingInfo.symbol))
            setTrades(trades_data)
        }, tradingInfo.refreshRate)

        return () => clearInterval(id)
    })

    const handleClick = (e: any) => {
        const index = e.target.id
        if (index !== active) {
            setActive(index)
        }
    }

    return (
        <div className='draggable_panel'>
            <div className='draggable_header' >
                {t('pages:trade_record_title')}
            </div>

            <Tabs>
                <Tab onClick={handleClick} active={active === '0'} id={'0'}>
                    {t('pages:trade_record_latest')}
                </Tab>

                <Tab onClick={handleClick} active={active === '1'} id={'1'}>
                    {t('pages:trade_record_mine')}
                </Tab>
            </Tabs>
            <>
                <Content active={active === '0'}>
                    <TradeRecordTable cols={cols} trades={trades}/>
                </Content>
                <Content active={active === '1'}>
                    <TradeRecordTable cols={cols} trades={trades}/>
                </Content>
            </>
        </div>
    )
}

export default TradeRecordsPanel
