import { createSlice, PayloadAction } from '@reduxjs/toolkit'

import  { AccountInfo } from 'defs/account_defs'
import { ConnectorNames } from 'defs/web3_defs'
import { AccountStatus } from 'defs/account_defs'

import { clear } from 'redux-localstorage-simple'

export interface Lv2Account {
  accAddr: string
  status: AccountStatus
  accInfo: AccountInfo
  isContractAddress: boolean
  apikey: string
  eddsaKey: any
  connectName: ConnectorNames
  connectNameTemp: ConnectorNames
}

const defaultAccId = process.env.REACT_APP_TEST_ACCOUND_ID ? parseInt(process.env.REACT_APP_TEST_ACCOUND_ID) : 0
const defaultApiKey = process.env.REACT_APP_TEST_API_KEY ? process.env.REACT_APP_TEST_API_KEY : ''
const defaultOwner = process.env.REACT_APP_TEST_OWNER ? process.env.REACT_APP_TEST_OWNER : ''
const defaultEddsaKey = process.env.REACT_APP_TEST_EDDSA_KEY ? JSON.parse(process.env.REACT_APP_TEST_EDDSA_KEY) : {publicKeyX: '', publicKeyY: '', secretKey: ''}

const defaultStatus = AccountStatus.UNCONNNECTED

const initialState = {
  accAddr: '',
  status: defaultStatus, 
  accInfo: {
    accountId: defaultAccId,
    owner: defaultOwner,
    publicKey: {},
    nonce: 0,
  },
  isContractAddress: false,
  apikey: defaultApiKey,
  eddsaKey: defaultEddsaKey, 
  connectName: ConnectorNames.Unknown,
  connectNameTemp: ConnectorNames.Unknown,
} as Lv2Account

const accountSlice = createSlice({
  name: 'account',
  initialState,
  reducers: {
    reset(state, action: PayloadAction<any>) {
      Object.assign(state, initialState)
    },
    setAccAddr(state, action: PayloadAction<string>) {
      state.accAddr = action.payload
    },
    setAccountInfo(state, action: PayloadAction<AccountInfo>) {
      state.accInfo = action.payload
    },
    setAccountStatus(state, action: PayloadAction<AccountStatus>) {
      state.status = action.payload
    },
    setEddsaKey(state, action: PayloadAction<any>) {
      state.eddsaKey = action.payload
    },
    setConnectName(state, action: PayloadAction<ConnectorNames>) {
      state.connectName = action.payload
    },
    setConnectNameTemp(state, action: PayloadAction<ConnectorNames>) {
      state.connectNameTemp = action.payload
    },
    setIsContractAddress(state, action: PayloadAction<boolean>) {
      state.isContractAddress = action.payload
    },
  },
})

export const { reset, setAccAddr, setConnectName, setConnectNameTemp, setAccountInfo, setAccountStatus, setEddsaKey, setIsContractAddress, } = accountSlice.actions
export default accountSlice
