import globalAxios, { AxiosPromise, AxiosInstance } from 'axios'
import { createRequestFunction } from 'lightcone_api/api/common'

import { GetApiKeyRequest, AuthApiKeyApiAxiosParamCreator } from 'lightcone_api/api'

import {
  BASE_PATH,
  COLLECTION_FORMATS,
  RequestArgs,
  BaseAPI,
  RequiredError,
} from 'lightcone_api/api/base'

import * as api from 'lightcone_api/api'

import { getEdDSASig, getEdDSASigWithPoseidon, getEcDSASig } from 'lightcone_api/api_wrapper/sign_tools'

class AuthApiKeyApi {

  localVarAxiosParamCreator: any
  conf: any

  constructor(conf: any) {
    this.conf = conf
    this.localVarAxiosParamCreator = api.AuthApiKeyApiAxiosParamCreator(conf)
  }

  // getOrders: async (accountId: number, market?: string, start?: number, end?: number, side?: 'BUY' | 'SELL', 
  // status?: 'processing' | 'processed' | 'failed' | 'cancelled' | 'cancelling' | 'expired', limit?: number, 
  // offset?: number, orderTypes?: 'LIMIT_ORDER' | 'MAKER_ONLY' | 'TAKER_ONLY' | 'class_amm', options: any = {})
  async getOrders(accountId: number, apiKey: string) {

    const localVarAxiosArgs = await this.localVarAxiosParamCreator.getOrders(accountId)

    const request = createRequestFunction(localVarAxiosArgs, globalAxios, BASE_PATH, this.conf)

    localVarAxiosArgs.options.headers['X-API-KEY'] = apiKey

    const response = await request()

    return response
  }

  async getUserTrades(accountId: number, apiKey: string) {

    const localVarAxiosArgs = await this.localVarAxiosParamCreator.getUserTrades(accountId)

    const request = createRequestFunction(localVarAxiosArgs, globalAxios, BASE_PATH, this.conf)

    localVarAxiosArgs.options.headers['X-API-KEY'] = apiKey

    const response = await request()

    return response
  }

  async getNextStorageId(accountId: number, sellTokenId: number, apiKey: string) {

    const localVarAxiosArgs = await this.localVarAxiosParamCreator.getNextStorageId(accountId, sellTokenId)

    const request = createRequestFunction(localVarAxiosArgs, globalAxios, BASE_PATH, this.conf)

    localVarAxiosArgs.options.headers['X-API-KEY'] = apiKey

    const response = await request()

    return response
  }

}

export { AuthApiKeyApi }
