import { useEffect, useState } from 'react'
import { useTranslation } from 'react-i18next'
import { useLocation } from 'react-router-dom'
import styles from './basic_info.module.scss'

import SymbolList from 'components/selects/SymbolList'

import { useGetTradingInfo } from 'stores/trading/hook'

import { authNoKeyApi } from 'lightcone_api/api_wrapper'
import { TickerData } from 'defs/exchange_defs'

import { getLocaleDt } from 'utils/dt_tools'

export interface BasicInfoPanelProps {
}

const BasicInfoPanel = ({ }: BasicInfoPanelProps) => {

    const { t } = useTranslation()

    const [ticker, setTicker] = useState<TickerData>()

    const location = useLocation()

    const tradingInfo = useGetTradingInfo()

    useEffect(() => {

        const id = setInterval(async () => {
            const tick_data = await authNoKeyApi.getTickerWrapper([tradingInfo.symbol])
            setTicker(tick_data[tradingInfo.symbol])
        }, tradingInfo.refreshRate)

        return () => {
            clearInterval(id)
        }
    })

    return (
        <div className='draggable_panel'>
            <div className={styles.basic_info_panel}>
                <div className={styles.basic_info_row}><SymbolList location={location} listData={tradingInfo.markets}></SymbolList></div>
                <div className={styles.basic_info_row}><div>{getLocaleDt(ticker?.date_time)}</div></div>
                <div className={styles.basic_info_row}><div>{t('pages:basic_24hrs_updown')}</div><div className={styles.basic_info_row2}></div></div>
                <div className={styles.basic_info_row}><div>{t('pages:basic_24hrs_low')}</div><div className={styles.basic_info_row2}>{ticker?.low}</div></div>
                <div className={styles.basic_info_row}><div>{t('pages:basic_24hrs_high')}</div><div className={styles.basic_info_row2}>{ticker?.high}</div></div>
                <div className={styles.basic_info_row}><div>{t('pages:basic_24hrs_volume')}({tradingInfo.symbol_quote})</div><div className={styles.basic_info_row2}>{ticker?.quote_token_amt}</div></div>
                <div className={styles.basic_info_row}><div>{t('pages:basic_24hrs_volume')}({tradingInfo.symbol_base})</div><div className={styles.basic_info_row2}>{ticker?.base_token_amt}</div></div>
            </div>
        </div>
    )
}

export default BasicInfoPanel
