import { useState, useEffect } from 'react'
import { useTranslation } from 'react-i18next'

import { Tabs, Tab, Content } from 'components/tabs/HTabs'
import OrderRecordTable from 'components/tables/OrderRecordTable'

import { useGetTradingInfo } from 'stores/trading/hook'
import { useGetLv2Account } from 'stores/account/hook'

import { authApiKeyApi } from 'lightcone_api/api_wrapper'
import { OrdersData } from 'defs/exchange_defs'

export interface OrderRecordsPanelProps {
}

const OrderRecordsPanel = ({ }: OrderRecordsPanelProps) => {

    const tradingInfo = useGetTradingInfo()

    const acc = useGetLv2Account()

    const { t } = useTranslation()

    const [orders, setOrders] = useState<OrdersData>()

    const [active, setActive] = useState('0')
    const handleClick = (e: any) => {
        const index = e.target.id
        if (index !== active) {
            setActive(index)
        }
    }

    const cols = [
        {
            Header: t('pages:order_col_1'),
            accessor: 'order_col_1',
        },
        {
            Header: t('pages:order_col_2'),
            accessor: 'order_col_2',
        },
        {
            Header: t('pages:order_col_3'),
            accessor: 'order_col_3',
        },
        {
            Header: t('pages:order_col_4'),
            accessor: 'order_col_4',
        },
        {
            Header: t('pages:order_col_5'),
            accessor: 'order_col_5',
        },
        {
            Header: t('pages:order_col_6'),
            accessor: 'order_col_6',
        },
        {
            Header: t('pages:order_col_7'),
            accessor: 'order_col_7',
        },
        {
            Header: t('pages:order_col_8'),
            accessor: 'order_col_8',
        },
    ]

    useEffect(() => {

        const id = setInterval(async () => {
            try {
                if (acc.account.accInfo) {
                    const orders_data = (await authApiKeyApi.getOrders(acc.account.accInfo.accountId, '')).data
                    setOrders(orders_data)
                }
            } catch(err) {
                return
            }
        }, tradingInfo.refreshRateSlow)

        return () => clearInterval(id)
    })

    return (
        <div className='draggable_panel'>
            <div className='draggable_header' >
                {t('pages:order_record_title')}
            </div>

            <Tabs>
                <Tab onClick={handleClick} active={active === '0'} id={'0'}>
                    {t('pages:order_record_current')}
                </Tab>

                <Tab onClick={handleClick} active={active === '1'} id={'1'}>
                    {t('pages:order_record_history')}
                </Tab>
            </Tabs>
            <>
                <Content active={active === '0'}>
                    <OrderRecordTable cols={cols} orders={orders}/>
                </Content>
                <Content active={active === '1'}>
                    <OrderRecordTable cols={cols} orders={orders}/>
                </Content>
            </>
        </div>
    );
};

export default OrderRecordsPanel
