import { useEffect, useState } from 'react'

import { Responsive, WidthProvider } from 'react-grid-layout'

import { rowHeight, layouts, breakpoints, cols } from 'layouts/responsive_layout/pages/trading_page'

import BasicInfoPanel from 'features/panels/trading/BasicInfoPanel'
import OrderActionsPanel from 'features/panels/trading/OrderActionsPanel'
import OrderBookPanel from 'features/panels/trading/OrderBookPanel'
import OrderRecordsPanel from 'features/panels/trading/OrderRecordsPanel'
import TradeRecordsPanel from 'features/panels/trading/TradeRecordsPanel'
import TradingChartPanel from 'features/panels/trading/TradingChartPanel'

import { useGetTradingInfo } from 'stores/trading/hook'

import { authNoKeyApi } from 'lightcone_api/api_wrapper'
import { DepthData } from 'defs/exchange_defs'

const ResponsiveGridLayout = WidthProvider(Responsive)

const TradingPage = () => {

  const tradingInfo = useGetTradingInfo()

  const [depth, setDepth] = useState<DepthData>()

  useEffect(() => {

      const id = setInterval(async () => {
          const depth_data = (await authNoKeyApi.getDepthWrapper(tradingInfo.symbol, 0, 20))
          setDepth(depth_data)
      }, tradingInfo.refreshRate)

      return () => clearInterval(id)
  })

  return (
    <ResponsiveGridLayout className='layout'
      layouts={layouts}
      rowHeight={rowHeight}
      draggableHandle={'.draggable_header'}
      breakpoints={breakpoints}
      isResizable={true}
      cols={cols}>
      <div key='basic_info'><BasicInfoPanel /></div>
      <div key='order_book'><OrderBookPanel depth={depth} /></div>
      <div key='trading_chart'><TradingChartPanel depth={depth} /></div>
      <div key='order_actions'><OrderActionsPanel/></div>
      <div key='trade_records'><TradeRecordsPanel /></div>
      <div key='order_records'><OrderRecordsPanel /></div>
    </ResponsiveGridLayout>
  )
}

export default TradingPage
